
.vj-option-block{
  display: block;
  position: absolute;
  background: #fff;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
          box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
  margin: 14px;
  z-index: 1000;
  top: -14px;
  left: 0;
}
.vj-option-name{
  padding: 7px 14px;
  color: #000;
  font-size: 14px;
}




