
.vj-input-display{
  min-width: 7px;
  display: inline-block;
}
.vj-input-block {
  display: inline-block;
}
.vj-input {
  width: 160px;
  border: 0;
  color: #444;
  background-color: #eee;
  outline:none;
  padding-left: 7px
}
.vj-input-literal{
  color: #0033cc
}
.vj-input-string{
  color: #c41a16
}
.vj-input-name{
  color:  #881391
}
.vj-input-normal{
  color: #444;
}




